<template>
  <div class="row justify-content-center">
    <div class="col-md-12">
      <br />

      <!--     -->

      <table class="table table-hover">
        <thead class="thead-light">
          <tr>
            <th class="col-md-1">No.</th>
            <th class="col-md-3"></th>
            <th class="col-md-3"></th>

            <!-- (  ) -->

            <th class="col-md-3"><div v-b-tooltip.hover title="         ."> </div></th>
            <th class="col-md-2"> </th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="item in items" v-bind:key="item.userIndex">
            <td class="col-md-1">{{ item.userIndex }}</td>
            <td class="col-md-3">{{ item.userName }}</td>
            <td class="col-md-3">{{ item.userId }}</td>

            <!--      -->

            <td class="col-md-3"><span class="review-router-span" v-on:click="viewUserReview(item.userIndex)"> </span></td>

            <!--    -->

            <td class="col-md-2">
              <input
                type="checkbox"
                v-on:click="setBlock(item.userIndex, item.isBlock)"
                v-model="item.isBlock"
              />

              <!--       -->

              <span v-if="item.isBlock == '1'">
                 
              </span>
              <span v-else>
                 
              </span>

            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {

    //put  

    // putisblock() {
    //   // axios   
    //   this.$axios
    //     .put("http://localhost:8090/team3/isblocktruefalse", {
    //       isBlock: this.isBlock,
    //     })
    //     .then(function() {
    //       console.log("  ");
    //     })
    //     .catch(function(err) {
    //       console.log("  ");
    //       console.log(err);
    //     });
    // },

    // 

    setBlock(index, block) {

      // 

      if (block == 0) {
        block = 1;
      }
      else {
        block = 0;
      }

      this.$axios
        .get("http://localhost:8090/team3/setblock", {
          params: {
            userIndex: index,
            isBlock: block,
          },
        })
        .then(function() {
          console.log("  ");
        })
        .catch(function(err) {
          console.log("  ");
          console.error(err);
        });
    },

    //    (ReviewDetail) 

    viewUserReview(userIndex) {
      this.$router.push({ name: "AppAdminReviewDetail", params: { userIndex: userIndex } });
    }
  },
  mounted() {
    let obj = this;

    obj.$axios
      .get("http://localhost:8090/team3/accountadminget")
      .then(function(res) {
        console.log("  ");
        obj.items = res.data;
      })
      .catch(function(err) {
        console.log("  ");
        console.log(err);
      });
  },
};
</script>

<style>
.review-router-span{
  font-weight: bold;
}
.review-router-span:hover {
  cursor: pointer;
  color: rgb(132, 196, 190);
  text-decoration: underline;
  transition: color .2s;
}
</style>
