<template>
  <div class="row justify-content-center">
    <div class="col-md-12">
      <br />

      <!--      -->

      <table class="table table-hover">
        <thead class="thead-light">
          <tr>
            <th class="col-md-1"> </th>
            <th class="col-md-2"> </th>
            <th class="col-md-2"></th>
            <th class="col-md-6"></th>
            <th class="col-md-1"></th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="item in items" v-bind:key="item.Index">
            <td class="col-md-1">{{ item.carIndex }}</td>
            <td class="col-md-2">{{ item.userIndex }}</td>
            <td class="col-md-2">{{ item.reviewDate }}</td>
            <td class="col-md-6">{{ item.reviewContents }}</td>
            
            <!--    -->

            <td class="col-md-1">
              <span
                class="delete-review"
                v-on:click="deleteReview(item.userIndex, item.carIndex)"
                ></span
              >
            </td>
          </tr>
        </tbody>

      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {

    //   

    deleteReview(user, car) {
      let obj = this;

      obj.$axios
        .get("http://localhost:8090/team3/reviewdelete", {

          // get 

          params: {
            userIndex: user,
            carIndex: car,
          },
        })
        .then(function() {
          console.log("  ");
        })
        .catch(function(err) {
          console.log("  ");
          console.log(err);
        });

      // 

      this.$router.go(this.$router.currentRoute);
    }
  },
  mounted() {
    let obj = this;

    obj.$axios
      .get("http://localhost:8090/team3/reviewadminget")
      .then(function(res) {
        console.log("  ");
        obj.items = res.data;
      })
      .catch(function(err) {
        console.log("  ");
        console.log(err);
      });
  },
};
</script>

<style>
.delete-review {
  color: red;
  transition: 0.2s;
}
.delete-review:hover {
  cursor: pointer;
  color: rgb(158, 26, 26);
}
</style>
