<template>
  <div class="row justify-content-center">
    <div class="col-md-12">
      <br />
      <table class="table table-hover">
        <!--     -->

        <thead class="thead-light">
          <tr>
            <th class="col-md-1">No.</th>
            <th class="col-md-2"></th>
            <th class="col-md-3"></th>
            <th class="col-md-2"></th>
            <th class="col-md-2"> </th>
            <th class="col-md-1"> </th>
          </tr>
        </thead>

        <!--  -->

        <tbody>
          <tr v-for="item in items" v-bind:key="item.carIndex">
            <td class="col-md-1">{{ item.carIndex }}</td>
            <td class="col-md-2"> {{ item.carIndex }}</td>
            <td class="col-md-3">{{ item.carModel }}</td>
            <td class="col-md-1">{{ item.carFuel }} %</td>

            <!--   -->

            <td class="col-md-2">
              <input
                type="checkbox"
                v-model="item.carClear"
                v-on:click="sendData(item.carIndex, item.carClear)"
              />
              <span v-if="item.carClear == '1'">
                 
              </span>
              <span v-else>
                 
              </span>
            </td>

            <!--      -->

            <td class="col-md-1">
              <button
                v-b-modal.carEdit
                id="car-add-btn"
                v-on:click="carInfoEdit(item.carIndex)"
              >
                 
              </button>
              <!--    () -->

              <!-- &nbsp;
              <button
                class="car-axios-button-delete"
                v-on:click="deleteCar(item.carIndex)"
              >
                
              </button>-->
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <div>
      <b-modal id="carEdit" centered title=" " size="lg">
        <!--    -->
        <!--          -->

        <span class="my-4">
          <!--   -->

          <b-form-group
            label-cols="4"
            label-cols-lg="2"
            label="  : "
            label-for="input-default"
          >
            <b-form-input
              list="input-list"
              id="input-with-list"
              v-model="edit.carModel"
            ></b-form-input>

            <b-form-datalist
              id="input-list"
              :options="carModelList"
            ></b-form-datalist>
          </b-form-group>

          <!--   -->

          <b-form-group
            label-cols="4"
            label-cols-lg="2"
            label="  : "
            label-for="input-default"
          >
            <b-form-input
              id="input-default"
              v-model="edit.carNumber"
            ></b-form-input>
          </b-form-group>
        </span>

        <span class="row">
          <!--  , ,  -->

          <!--   -->
          <span class="col-5">
            <b-form-group
              label-cols="4"
              label-cols-lg="5"
              label="  : "
              label-for="input-default"
              ><b-form-input
                v-model="edit.carTakePerson"
                type="number"
              ></b-form-input>
            </b-form-group>
          </span>

          <!--    -->
          <span class="col-4">
            <b-form-group
              label-cols="4"
              label-cols-lg="4"
              label=" : "
              placeholder="%"
              label-for="input-default"
              ><b-form-input
                v-model="edit.carFuel"
                type="number"
              ></b-form-input>
            </b-form-group>
          </span>

          <!--  -->
          <span class="col-3">
            <b-form-group
              label-cols="4"
              label-cols-lg="7"
              label="  : "
              label-for="input-checkbox"
            >
              <input
                type="checkbox"
                v-model="edit.carClear"
                style="zoom:2.5; cursor:pointer; margin-top:2px"
              />
            </b-form-group>
          </span>
        </span>

        <!-- , ,    -->

        <p>
          <!--  -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label=" : "
              label-for="input-default"
            >
              <b-form-textarea
                id="textarea-default"
                v-model="edit.carOption1"
              ></b-form-textarea
            ></b-form-group>
          </span>

          <!--  -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label=" : "
              label-for="input-default"
            >
              <b-form-textarea
                id="textarea-default"
                v-model="edit.carOption2"
              ></b-form-textarea
            ></b-form-group>
          </span>

          <!--  -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label=" : "
              label-for="input-default"
            >
              <b-form-textarea
                id="textarea-default"
                v-model="edit.carOption3"
              ></b-form-textarea
            ></b-form-group>
          </span>

          <!--   url   -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label="  URL : "
              label-for="input-default"
            >
              <b-form-input
                id="input-default"
                v-model="edit.carUsage"
              ></b-form-input>
            </b-form-group>
          </span>

          <!--    -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label="  : "
              label-for="input-default"
            >
              <b-form-input
                id="input-default"
                v-model="edit.carLocation"
              ></b-form-input>
            </b-form-group>
          </span>
        </p>

        <!-- modal (    ) -->

        <template #modal-footer="{ cancel }">
          <button id="modal-cancel" v-on:click="cancel()">
            
          </button>
          <button
            id="modal-submit"
            v-on:click="
              carEditSubmit(
                edit.carIndex,
                edit.carModel,
                edit.carNumber,
                edit.carTakePerson,
                edit.carClear,
                edit.carFuel,
                edit.carOption1,
                edit.carOption2,
                edit.carOption3,
                edit.carUsage,
                edit.carLocation
              )
            "
          >
            
          </button>
        </template>
      </b-modal>

      <!--    (    modal  ) -->

      <b-button v-b-modal.carAdd id="car-add-btn"> </b-button>
      <b-modal id="carAdd" centered title="  " size="lg">
        <!--     -->
        <!--          -->

        <span class="my-4">
          <!--   -->

          <b-form-group
            label-cols="4"
            label-cols-lg="2"
            label="  : "
            label-for="input-default"
          >
            <b-form-input
              list="input-list"
              id="input-with-list"
              v-model="carModel"
            ></b-form-input>

            <b-form-datalist
              id="input-list"
              :options="carModelList"
            ></b-form-datalist>
          </b-form-group>

          <!--   -->

          <b-form-group
            label-cols="4"
            label-cols-lg="2"
            label="  : "
            label-for="input-default"
          >
            <b-form-input id="input-default" v-model="carNumber"></b-form-input>
          </b-form-group>
        </span>

        <span class="row">
          <!--  , ,  -->

          <!--   -->
          <span class="col-5">
            <b-form-group
              label-cols="4"
              label-cols-lg="5"
              label="  : "
              label-for="input-default"
              ><b-form-input
                v-model="carTakePerson"
                type="number"
              ></b-form-input>
            </b-form-group>
          </span>

          <!--    -->
          <span class="col-4">
            <b-form-group
              label-cols="4"
              label-cols-lg="4"
              label=" : "
              placeholder="%"
              label-for="input-default"
              ><b-form-input v-model="carFuel" type="number"></b-form-input>
            </b-form-group>
          </span>

          <!--  -->
          <span class="col-3">
            <b-form-group
              label-cols="4"
              label-cols-lg="7"
              label="  : "
              label-for="input-checkbox"
            >
              <input
                type="checkbox"
                v-model="carClear"
                style="zoom:2.5; cursor:pointer; margin-top:2px"
              />
            </b-form-group>
          </span>
        </span>

        <!-- , ,    -->

        <p>
          <!--  -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label=" : "
              label-for="input-default"
            >
              <b-form-textarea
                id="textarea-default"
                v-model="carOption1"
              ></b-form-textarea
            ></b-form-group>
          </span>

          <!--  -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label=" : "
              label-for="input-default"
            >
              <b-form-textarea
                id="textarea-default"
                v-model="carOption2"
              ></b-form-textarea
            ></b-form-group>
          </span>

          <!--  -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label=" : "
              label-for="input-default"
            >
              <b-form-textarea
                id="textarea-default"
                v-model="carOption3"
              ></b-form-textarea
            ></b-form-group>
          </span>

          <!--   url   -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label="  URL : "
              label-for="input-default"
            >
              <b-form-input
                id="input-default"
                v-model="carUsage"
              ></b-form-input>
            </b-form-group>
          </span>

          <!--    -->

          <span>
            <b-form-group
              label-cols="4"
              label-cols-lg="2"
              label="  : "
              label-for="input-default"
            >
              <b-form-input
                id="input-default"
                v-model="carLocation"
              ></b-form-input>
            </b-form-group>
          </span>
        </p>

        <!-- modal (    ) -->

        <template #modal-footer="{ cancel }">
          <button id="modal-cancel" v-on:click="cancel()">
            
          </button>
          <button id="modal-submit" v-on:click="carInfoAdd"></button>
        </template>
      </b-modal>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
      edit: [],

      //   

      carModelList: [
        "  AD",
        "K5 3()",
        "  K3",
        "  ",
        "K5 3",
      ],

      //   

      carIndex: "",
      carModel: "",
      carNumber: "",
      carTakePerson: "",
      carFuel: "",
      carClear: 0,
      carOption1: "",
      carOption2: "",
      carOption3: "",
      carUsage: "",
      carLocation: "",
    };
  },
  methods: {
    //        

    sendData(Index, Clear) {
      // 

      if (Clear == 1) {
        Clear = 0;
      } else {
        Clear = 1;
      }

      this.$axios
        .get("http://localhost:8090/team3/carinfoupdate", {
          // get 

          params: {
            carIndex: Index,
            carClear: Clear,
          },
        })
        .then(function() {
          console.log("   ");
        })
        .catch(function(err) {
          console.log("   ");
          console.log(err);
        });
    },

    //  

    carInfoAdd() {
      let obj = this;

      obj.$axios
        .post("http://localhost:8090/team3/carinsert", {
          //    

          carNumber: this.carNumber,
          carModel: this.carModel,
          carTakePerson: this.carTakePerson,
          carFuel: this.carFuel,
          //  
          carClear: this.carClear == true ? 1 : 0,
          carOption1: this.carOption1,
          carOption2: this.carOption2,
          carOption3: this.carOption3,
          carUsage: this.carUsage,
          carLocation: this.carLocation,
        })
        .then(function() {
          console.log("   ");
        })
        .catch(function(err) {
          console.log("   ");
          console.log(err);
        });

      // 
    },
    carInfoEdit(carIndex) {
      let obj = this;

      obj.$axios
        .get("http://localhost:8090/team3/careditinfo", {
          params: {
            carIndex: carIndex,
          },
        })
        .then(function(res) {
          console.log("   ");
          obj.edit = res.data;
        })
        .catch(function(err) {
          console.log("   ");
          console.log(err);
        });
    },
    carEditSubmit(
      carIndex,
      carModel,
      carNumber,
      carTakePerson,
      carClear,
      carFuel,
      carOption1,
      carOption2,
      carOption3,
      carUsage,
      carLocation
    ) {
      this.$axios
        .post("http://localhost:8090/team3/careditsubmit", {
          carIndex: carIndex,
          carModel: carModel,
          carNumber: carNumber,
          carTakePerson: carTakePerson,
          carClear: carClear == true ? 1 : 0,
          carFuel: carFuel,
          carOption1: carOption1,
          carOption2: carOption2,
          carOption3: carOption3,
          carUsage: carUsage,
          carLocation: carLocation,
        })
        .then(function() {
          console.log("  ");
        })
        .catch(function(err) {
          console.log("  ");
          console.log(err);
        });
    },

    //   ()

    // // deleteCar(car) {
    // //   let obj = this;

    // //   obj.$axios
    // //     .get("http://localhost:8090/team3/cardelete", {
    // //       params: {
    // //         carIndex: car,
    // //       },
    // //     })
    // //     .then(function() {
    // //       console.log("   ");
    // //     })
    // //     .catch(function(err) {
    // //       console.log("   ");
    // //       console.log(err);
    // //     });
    // // },
  },
  mounted() {
    let obj = this;

    obj.$axios
      .get("http://localhost:8090/team3/caradminget")
      .then(function(res) {
        console.log("   ");
        obj.items = res.data;
      })
      .catch(function(err) {
        console.log("   ");
        console.log(err);
      });
  },
};
</script>

<style>
.car-axios-button,
.car-axios-button-delete {
  color: white;
  border: none;
  border-radius: 5px;
  box-shadow: 1.5px 1.5px 1.5px black;
  transition-duration: 0.15s;
}

.car-axios-button {
  background-color: rgb(132, 194, 189);
}

.car-axios-button:hover {
  background-color: rgb(90, 129, 126);
}

.car-axios-button-delete {
  background-color: rgb(199, 23, 23);
}

.car-axios-button-delete:hover {
  background-color: rgb(146, 39, 39);
}

#car-form-padding {
  padding-top: 8px;
  padding-bottom: 8px;
}

input[id="input-checkbox"] {
  zoom: 2;
  cursor: pointer;
}

#car-add-btn {
  color: white;
  border: none;
  border-radius: 5px;
  box-shadow: 1.5px 1.5px 1.5px black;
  transition-duration: 0.15s;
  background-color: rgb(132, 194, 189);
  border: none;
}
#car-add-btn:hover,
#car-add-btn:active {
  background-color: rgb(90, 129, 126);
}

#modal-cancel {
  background-color: rgb(199, 23, 23);
  border: none;
  border-radius: 5px;
  color: white;
  width: 60px;
  height: 30px;
  transition: background-color 0.2s;
}

#modal-cancel:hover {
  background-color: rgb(146, 39, 39);
}

#modal-submit {
  background-color: rgb(132, 194, 189);
  border: none;
  border-radius: 5px;
  color: white;
  width: 60px;
  height: 30px;
  transition: background-color 0.2s;
}

#modal-submit:hover {
  background-color: rgb(90, 129, 126);
}
</style>
