<template>
    <div class="mypage-body">
        <div class="container-fluid">
            <div class="CardReg-header">
                <button type="button" v-on:click="moveMyPage" style="margin-bottom: 50px"></button>
                <h2 style="font-weight: bold;"></h2>
            </div>
            <div class="CardReg-CardNum">
                <p></p>
                <input type="text" v-model="userCardNum" placeholder="  '-' ." style="width: 95%; border: none; border-bottom: 1px solid silver; font-size: 20px;" maxlength="16" oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"  @change="CardNumFormat()">
                <div v-if="!cardNumValidCheck() && userCardNum" style="color: red;">
                 16 .
                </div>
            </div>
            <div class="CardReg-CardValidity">
                <p></p>
                <input type="text" v-model="userCardValidity" placeholder="MM/YY" style="width: 90%; border: none; border-bottom: 1px solid silver; font-size: 20px;"
                @change="validityFormat()" oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" maxlength="4">
            </div>
            <div class="CardReg-CardPw">
                <p></p>
                <input type="password" v-model="userCardPw" placeholder="  2" style="width: 90%; border: none; border-bottom: 1px solid silver; font-size: 20px;" oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" maxlength="2">
            </div>
            <div class="CardGuidelines">
                <p></p>
                <!-- Button trigger modal -->
                <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#exampleModal">
                   
                </button>

                <!-- Modal -->
                <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog" style="width: 100%">
                    <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel"></h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                          35(  )       ,     .<br><br>
                        1.      <br>
                          (  ) , ,          , ( )  .<br>
                        ,         .<br><br>
                        2.    <br>
                        ID   ,       .<br>
                          ,     .<br><br>
                        3.            ,   ,        ,  (1332)    .
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                    </div>
                    </div>
                </div>
                </div>
            </div>
            <div class="CardReg-register">
                <button type="button" class="card-success" v-on:click="moveReg"></button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            userIndex: 0,
            userCardNum: '',
            userCardValidity: '',
            userCardPw: ''
        }     

    },
    mounted() {
        this.userIndex = sessionStorage.getItem('_UserConn-INDEX')
    },
    methods: {
        moveMyPage() {
            this.$router.push({ name: "AppUserMyPage" })
        },
        moveReg() {
            let obj = this;
            
            //  
            obj.$axios.put('http://localhost:8090/vue/cardReg', {
                userIndex: obj.userIndex,
                userCardNum: obj.userCardNum,
                userCardValidity: obj.userCardValidity,
                userCardPw: obj.userCardPw 
            })
            .then(function(res) {
                console.log(res);
                
                obj.$router.push({ name: "AppUserMyPage" })
            })
            .catch(function(error) {
                console.error(error);
            })
        },
        validityFormat: function() {
            let mm = this.userCardValidity.substring(0, 2);
            let yy = this.userCardValidity.substring(2, 4);
            if((mm.length + yy.length) == 4)
                this.userCardValidity = mm + "/" + yy
        },
        cardNumValidCheck() {
            if(this.userCardNum.length === 16 || this.userCardNum.length === 19) {
                return true;
            } else {
                return false;
            }
        },
        CardNumFormat: function() {
            let first = this.userCardNum.substring(0, 4);
            let second = this.userCardNum.substring(4, 8);
            let third = this.userCardNum.substring(8, 12);
            let fourth = this.userCardNum.substring(12, 16);

            if((first.length + second.length + third.length + fourth.length) == 16) {
                this.userCardNum = first + "-" + second + "-" + third + "-" + fourth
            }
        }
    }
}
</script>

<style scoped>
    .mypage-body {
        margin: 0 auto;
        width: 900px;
    }

    .container-fluid {
        display: grid;
        grid-template-areas: 
            'RegHeader RegHeader RegHeader RegHeader'
            'RegCardNum RegCardNum RegCardNum RegCardNum'
            'RegCardValidity RegCardValidity RegCardPw RegCardPw'
            'Guidelines Guidelines Guidelines Guidelines'
            'RegCardRegister RegCardRegister RegCardRegister RegCardRegister'
        ;
    }

    .CardReg-header {
        grid-area: RegHeader;
        margin-bottom: 30px;
        border-bottom: 2px solid #84C2BD;
        text-align: left;
    }

    .CardReg-CardNum {
        grid-area: RegCardNum;
        text-align: left;
        font-weight: bold;
    }

    .CardReg-CardValidity {
        grid-area: RegCardValidity;
        text-align: left;
        margin-top: 50px;
        font-weight: bold;
    }

    .CardReg-CardPw {
        grid-area: RegCardPw;
        text-align: left;
        margin-top: 50px;
        font-weight: bold;
    }

    .CardGuidelines {
        grid-area: Guidelines;
        text-align: left;
        margin-top: 80px;
        font-weight: bold;
    }

    .CardReg-register {
        grid-area: RegCardRegister;
        text-align: left;  
    }

    .modal-title {
        font-weight: bold;
        padding: 0px 0px 10px 10px;
    }

    .modal-body {
        padding: 10px;
        text-align: left;
        font-size: 13px;
        font-weight: normal;
    }

    .btn-primary {
        background-color: silver;
        border: none;
    }

    .card-success {
        width: 100%;
        border: none;
        background-color: #84C2BD;
        border-radius: 7px;
        padding: 12px;
        color: white;
        margin-top: 70px;
    }

</style>