<template>
  <div class="mypage-body">
      <h3 style="margin-bottom: 50px; margin-top: 40px; border-bottom: 3px solid #84C2BD; padding-bottom: 30px;"></h3>
      <div class="Sign-contents">
        <label for="userId">
           * 
          <span v-if="checkFlag && !userId" style="color: #84C2BD; margin-left: 10px;">
             
          </span>
          <span v-if="!isUserIdValid && userId" style="color: red; margin-left: 10px;">
              
          </span>
          <span v-if="isVerifyEmail == 2 && userId" style="color:red; margin-left: 10px;">
             
          </span>
          <span v-if="isVerifyEmail == 1 && isUserIdValid" style="color: #84C2BD; margin-left: 10px;">
              
          </span>
        </label>
        <br>
        <input type="email" id="userId" name="userId" v-model="userId" placeholder="" style="width: 80%; height: 40px; margin-right: 20px;">
        <button type="button" @click="verifyEmail()" style="background-color: #84C2BD; color: white; height: 40px;border-radius: 5px; border: 1px solid silver"></button>
      </div>
      <div class="Sign-contents">
        <label for="userPw">
           *
          <span v-if="checkFlag && !userPw" style="color: #84C2BD; margin-left: 10px;">
             
          </span>
          <span v-if="!CheckPw() && userPw" style="color: red; margin-left: 10px;">8  </span>
        </label>
        <br>
        <input type="password" id="userPw" name="userPw" v-model="userPw" maxlength="16" placeholder="8  " style="width: 80%; height: 40px;">
      </div>
      <div class="Sign-contents">
        <label for="userPw">
            *
          <span v-if="!passwordCheckFlag" style="color: red; margin-left: 10px;">
            .
        </span>
        </label>
        <br>
        <input type="password" id="passwordCheck" name="passwordCheck" v-model="passwordCheck" maxlength="16" @blur="passwordCheckValid" placeholder=" " style="width: 80%; height: 40px">
      </div>
      <div class="Sign-contents1">
        <label for="userNickName">
           *
          <span v-if="checkFlag && !userNickName" style="color: #84C2BD; margin-left: 10px;">
           
        </span>
        </label>
        <br>
        <input type="text" id="userNickName" name="userNickName" v-model="userNickName" placeholder="" style="height: 40px">
      </div>
      <div class="Sign-contents1">
        <label for="userName">
           *
          <span v-if="checkFlag && !userName" style="color: #84C2BD; margin-left: 10px;">
           
        </span>
        </label>
        <br>
        <input type="text" id="userName" name="userName" v-model="userName" placeholder="" style="height: 40px; ">
      </div>
      
      <div class="Sign-contents">
        <label for="userRegNum">
            *
          <span v-if="checkFlag && !userRegNum" style="color: #84C2BD; margin-left: 10px;">
            
        </span>
        </label>
        <br>
        <input type="text" id="userRegNum" name="userRegNum" v-model="userRegNum" placeholder="" style="height: 40px;"
        oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" maxlength="6"> 
        <input type="text" maxlength="1" id="userRegNumGender" name="userRegNumGender"
        v-model="userRegNumGender" style="height: 40px; width: 40px;"
        oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');">
        <span style="font-size: 30px"></span>
      </div>
      <div class="Sign-contents">
        <label for="userPhone">
           *
          <span v-if="checkFlag && !this.userPhone" style="color: #84C2BD; margin-left: 10px;">
            .
        </span>
        <span v-if="!checkPhoneNumber() && this.userPhone.length > 0" style="color:#84c2bd; margin-left: 10px;">
             .
        </span>
        </label>
        <br>
        <input type="text" id="userPhone" name="userPhone" v-model="userPhone" placeholder="  (' - ')" style="width: 50%; height: 40px" maxlength="11" @focus="checkFlag = false"
        oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');">
      </div>
      <div style="margin-top: 30px">
        <button type="button" @click="moveJoin" style="background-color: #84C2BD; color: white; height: 40px;border-radius: 5px; margin-right: 20px; border: 1px solid silver; border: 1px solid silver; padding: 0px 40px 0px 40px;"></button>
        <button type="button" v-on:click="moveMain" style="background-color: #84C2BD; color: white; height: 40px;border-radius: 5px; border: 1px solid silver; padding: 0px 50px 0px 50px;">
          
        </button>
      </div>
  </div>
</template>

<script>

export default {
    data() {
        return {
            userId: '',
            userPw: '',
            userName: '',
            userRegNum: '',
            userRegNumGender: '',
            userNickName: '',
            userPhone: '',
            passwordCheck: null,
            passwordCheckFlag: true,
            checkFlag: false,
            isVerifyEmail: 0,
            licenseType: '',
            licenseNum: '',
            licensePassDate: ''
        }
    },
    methods: {
        moveJoin() {
          this.checkFlag = true;
          if(
            !this.isEmpty(this.userName)&&
            !this.isEmpty(this.userRegNum)&&
            !this.isEmpty(this.userPhone)&&
            !this.isEmpty(this.userId)&&
            !this.isEmpty(this.userNickName)&&
            !this.isEmpty(this.userPw)&&
            !this.isEmpty(this.userPhone)
            ){
            let obj = this;

            // 
            obj.$axios.post('http://localhost:8090/vue/userJoin', {
              userId: this.userId,
              userPw: this.userPw,
              userName : this.userName,
              userRegnum : this. userRegNum,
              userRegnumGender : this.userRegNumGender,
              userNickname : this.userNickName,
              userPhone : this.userPhone
            })
            .then(function(res) {
                console.log(res)

                //     

                obj.$router.push({ name: "AppMain" });
            })
            .catch(function(err) {
                console.log("  ");
                console.log(err);
            });
          }  
        },
        moveMain() {
            this.$router.push({ name: 'AppMain' });
        },
        passwordCheckValid() {
          if (this.userPw === this.passwordCheck) {
            this.passwordCheckFlag = true
          } else {
            this.passwordCheckFlag = false
          }
        },
        isEmpty(data) {
          if(data === "" || data === null || data === undefined || this.userPw != this.passwordCheck || !this.isUserIdValid || (this.isVerifyEmail == 0 || this.isVerifyEmail == 2) || this.checkPhoneNumber() == 0 || this.CheckPw() == 0) {
            return true;
          } else {
            return false;
          }
        },
        
        verifyEmail() {
            let thisVue = this
            thisVue.$axios.get('http://localhost:8090/vue/verifyEmail', {
            params: {
                userId: this.userId
            }
            }).then(function(response) {
                if(response.data == 0) {
                    thisVue.isVerifyEmail = 1
                }
                else
                    thisVue.isVerifyEmail = 2
            }).catch(function(error) {
            console.error(error)
            })
        },
        validateEmail(email) {
            var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(String(email).toLowerCase())
        },
        
        checkPhoneNumber() {
          if(this.userPhone.length == 11) {
            return true;
          } else {
            return false;
          }
        },

        CheckPw() {
          if(this.userPw.length >= 8) {
            return true;
          } else {
            return false;
          }
        }
    },
    computed: {
      isUserIdValid() {
        return this.validateEmail(this.userId);
      }
    }
}
</script>

<style>

  .mypage-body {
    margin: 0 auto;
    width: 900px;
  }

  .Sign-contents {
    text-align: left;
    margin-left: 110px;
    margin-top: 20px;
  }

  .Sign-contents1 {
    text-align: left;
    margin-left: 110px;
    margin-top: 40px;
  }
</style>
