<template>
  <div class="row justify-content-center">
    <div class="col-md-12">
      <br />
      <table class="table table-hover">
        <thead class="thead-light">
          <tr>
            <th class="col-md-2"> </th>
            <th class="col-md-2"></th>
            <th class="col-md-6"></th>
            <th class="col-md-2"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in items" v-bind:key="item.Index">
            <td class="col-md-2">{{ item.carIndex }}</td>
            <td class="col-md-2">{{ item.reviewDate }}</td>
            <td class="col-md-6">{{ item.reviewContents }}</td>

            <!--    -->

            <td class="col-md-2">
              <span
                class="delete-review"
                v-on:click="deleteReview(item.userIndex, item.carIndex)"
                ></span
              >
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {

    //   (ReviewAdmin )

    deleteReview(user, car) {
      let obj = this;

      obj.$axios
        .get("http://localhost:8090/team3/reviewdelete", {

          // param (get )

          params: {
            userIndex: user,
            carIndex: car,
          },
        })
        .then(function() {
          console.log("  ");
          alert("  .");
          obj.$router.push({ name: "AppAdminMain" });
        })
        .catch(function(err) {
          console.log("  ");
          console.log(err);
        });
    },
  },
  mounted() {
    let obj = this;

    obj.$axios
      .get("http://localhost:8090/team3/reviewadminuserget", {

        // AccountAdmin   userIndex 

        params: {
          userIndex: obj.$route.params.userIndex,
        },
      })
      .then(function(res) {
        console.log("  ");
        obj.items = res.data;
      })
      .catch(function(err) {
        console.log("  ");
        console.log(err);
      });
  },
};
</script>

<style></style>
